/* timing function */

/**
 * CSS cubic-bezier timing functions
 * http://bourbon.io/docs/#timing-functions
 */

.at-list {
  border-top: 0.02133rem #e2ecf4 solid;
  position: relative;
  background-color: white;
}

.at-list::after {
  content: " ";
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  background-color: #e2ecf4;
  /* prettier-ignore */
  height: 1PX;
}

.at-list::before {
  content: " ";
  left: 0;
  width: 0.512rem;
  z-index: 100;
  position: absolute;
  background-color: transparent;
  /* prettier-ignore */
  height: -webkit-calc(100% - 1PX);
  height: calc(100% - 1PX);
  top: 0;
}

